   import pftabBar from '../../pages/public/tabBar/tabBar.vue'
	export default {
		data() {
			return {
				title:'购物车',
				totalGoodsNumber:1,
				totalPrice:200,
				isTotalChecked:false,
				isManage:true,
				cartList:[{
					       brand:'JOMO',
						   coupon:'套餐',
						   checked:true,
						   value:'0',
						   isTotalChecked:false, //是否全选
						   data:[{
							  name:'Meizu/魅族 Note9高通骁龙675处 高通骁龙675处理器4800万AI双摄大电池Meizu/魅族 Note9 高通骁',
							  value:'0',
							  checked:true,
							  totalPrice:200.00,
							  totalUnitPrice:200.00,
							  goodsNumber:1,
							  img:'../../static/mojo_07.png'
							},{
							  name:'Meizu/魅族 Note9高通骁龙675处 高通骁龙675处理器4800万AI双摄大电池Meizu/魅族 Note9 高通骁',
							  value:'0',
							  checked:false,
							  totalPrice:200.00,
							  totalUnitPrice:200.00,
							  goodsNumber:1,
							  img:'../../static/mojo_07.png'
							}]
					      },{
					       brand:'JOMO',
						   coupon:'套餐',
						   checked:false,
						   value:'0',
						   data:[{
							  name:'Meizu/魅族 Note9高通骁龙675处 高通骁龙675处理器4800万AI双摄大电池Meizu/魅族 Note9 高通骁',
							  value:'0',
							  checked:false,
							  totalPrice:400.00,
							  totalUnitPrice:400.00,
							  goodsNumber:1,
							  img:'../../static/mojo_07.png'
							}]
					      }
				 ]
			}
		},
		components:{
			pftabBar
		},
		onLoad(e) { //获取数的
			console.log(e)
		},
		created() {
        
        },
		methods: {
		  goTo(url){
			  uni.navigateTo({
			  	url:url
			  })
		  },
		  manageClik(){ //点击管理
			 this.isManage=false 
		  },
		  completeClik(){//点击完成
			   this.isManage=true 
		  },
	      inpuntClik(index,idx){ //修改数量
		   console.log("=============")
			this.cartList[index].data[idx].totalPrice=parseFloat(this.cartList[index].data[idx].totalUnitPrice*this.cartList[index].data[idx].goodsNumber)
			console.log(this.cartList[index].data[idx].goodsNumber+"=======")
			console.log(this.cartList[index].data[idx].totalPrice)
			this.updataAll()
		  },
          subtaractClick(index,idx){ //点击减少
			  if(this.cartList[index].data[idx].goodsNumber>1){
				  this.cartList[index].data[idx].goodsNumber=parseFloat(parseInt(this.cartList[index].data[idx].goodsNumber)-1)
				this.cartList[index].data[idx].totalPrice=parseFloat(this.cartList[index].data[idx].totalUnitPrice*this.cartList[index].data[idx].goodsNumber);
				this.updataAll()
			  }
		  },
		  addClick(index,idx){ //点击曾加
		     this.cartList[index].data[idx].goodsNumber=parseFloat(parseInt(this.cartList[index].data[idx].goodsNumber)+1)
			 this.cartList[index].data[idx].totalPrice=parseFloat(this.cartList[index].data[idx].totalUnitPrice*this.cartList[index].data[idx].goodsNumber);
			 this.updataAll()
		  },
		  radiogroup(index,idx){ //单项选择
			  this.cartList[index].data[idx].checked=this.cartList[index].data[idx].checked?false:true;
			  for(let i=0;i<this.cartList[index].data.length;i++){
					if(this.cartList[index].data[i].checked){ //如果有一项为真结束
						this.cartList[index].checked=true;
						break;
					}else{
					  this.cartList[index].checked=false;	
					}
			  }
			  this.updataAll();
		  },
		  updataAll(){ //全选图标
			  let Arr=this.cartList;
			  this.accountingPrice(); //计算价格数量
			  for(let i=0;i<Arr.length;i++){
				  for(let j=0;j<Arr[i].data.length;j++){
						 if(!Arr[i].data[j].checked){  //处理全选图标
							 this.isTotalChecked=false;
							 return; //跳出方法
						  }else{
							  this.isTotalChecked=true;
						  }
				  }
			  }
		  },
		  /**
		   * @param {Object} item  购物数组
		   * @param {Object} index  商家索引
		   * @param {Object} idx    商品索引
		   */
		  updateSelect(item,index,idx){//全选和头部点击处理
			  if(index==-1){ //全选图标处理
				  this.isTotalChecked=this.isTotalChecked ? false:true;
			  }
			  if(index!=-1){
				   item[index].checked=item[index].checked ? false : true; 
			  }
			  for(let i=0;i<item.length;i++){
				 if(index==-1){ //全选处理
					  if(!this.isTotalChecked){ 
						item[i].checked=false
					  }else{
						item[i].checked=true;
					  } 
					}else{//单选处理
					  
					}
				for(let j=0;j<item[i].data.length;j++){
					if(index==-1){ 
					  if(!this.isTotalChecked){ //选择
					      item[i].data[j].checked=false;	
					  }else{
					      item[i].data[j].checked=true;
					  }
					}else{ //单选处理
					   if(j>=item[index].data.length) break;
						if(item[index].checked){
							item[index].data[j].checked=true;
						}else{
							item[index].data[j].checked=false;
						}
					}
				 }
			  }
			    this.cartList=item;
			    this.updataAll();
		   },
		  accountingPrice(){ //计算价格数量
		     let Arr=this.cartList;
		     let totalPrice=0;
			 let totalGoodsNumber=0;
			 for(let i=0;i<Arr.length;i++){
				  for(let j=0;j<Arr[i].data.length;j++){
					  if(Arr[i].data[j].checked){
						  totalPrice+=Arr[i].data[j].totalPrice
						  totalGoodsNumber+=parseInt(Arr[i].data[j].goodsNumber)
					   }	
				  }
			} 
		     this.totalPrice=totalPrice; //价格
			 this.totalGoodsNumber=totalGoodsNumber; //数量
		 }  
		},
	}
